@import './styles/_variables';
@import './components/Join/_join.styles';
@import './components/Chat/chat.styles';

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style-type: none;
}

body {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $bgc-primary;
}

.container {
    width: 800px;
    height: 600px;
    display: flex;
    flex-flow: column;
    justify-content: space-evenly;
    align-items: center;
    background-color: $bgc-secondary;
    border-radius: 10px;
    box-shadow: 0 1px 10px 1px rgba(0, 0, 0, 0.3);
}

.chat-container {
    width: 500px;
    height: 500px;
}