.form {
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    color: $font-primary;
}

* input {
    margin-bottom: 35px;
    width: 300px;
    height: 35px;
    transition: all 0.3s ease;
    text-align: center;

}

* input:focus {
    outline: none;
    border: 1px solid $btn-color;
    background-color: rgba(26, 154, 158, 1);
    box-shadow: 0 1px 10px 1px rgba(255, 255, 255, 0.3);
}

.btn {
    width: 300px;
    height: 35px;
    border: none;
    outline: none;
    background-color: $btn-color;
    border-radius: 5px;
}