.chat {

    &__messages {
        padding: 40px;
        width: 70%;
        height: 100%;
        display: flex;
        flex-flow: column;
        justify-content: space-between;
        background-color: rgb(29, 29, 29);
    }

    &__message {
        color: white;
        margin-bottom: 15px;
    }

    &__users {
        padding: 20px;
        width: 30%;
        height: 100%;
        background-color: rgb(61, 61, 61);
        display: flex;
        flex-flow: column;
    }

    &__room-name {
        text-align: center;
        margin-bottom: 30px;
    }

    &__user {
        margin-bottom: 10px;
        color: black;
    }

    &__text {
        margin-left: 15px;
        padding: 5px 10px;
        padding-left: 15px;
        border-radius: 10px;
        font-size: 16px;
        background-color: rgb(189, 187, 187);
        color: black;
        position: relative;

        img {
            position: absolute;
            left: 1%;
            top: 50%;
            transform: translateY(-50%);
        }
    }

    &__time {
        margin-left: 8px;
        color: black;
        font-style: italic;
        font-size: 10px;
    }

}




.message-input {
    align-self: center;

    input &__input {
        text-align: left;
    }

    &__btn {
        width: 70px;
        height: 35px;
        border: none;
        background-color: rgb(41, 112, 204);
    }
}


.chat-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
}

.messages {
    height: 70%;
    width: 100%;
}